import React,{Component} from 'react';
import axios from 'axios';
import MovieClickInfo from './../MovieClickInfo/MovieClickInfo';
import Actors from './../Actors/Actors';
import './MovieInfo.css';
import Loader from '../Loader/Loader';

class MovieInfo extends Component{

    
    state = {
        movieInfo : null,
        actors:null
    };
    
    getMovieInfo = () => {
        const movieId = this.props.match.params.movieId;
        axios.get(`https://api.themoviedb.org/3/movie/${movieId}?api_key=f3e796f7e1ac95620354ed26f063ba80&language=en-US`)
        .then(response =>{
              this.setState({
                movieInfo : response.data,
              });
        })
    };

    getActorInfo = () => {
        const movieId = this.props.match.params.movieId;
        axios.get(` https://api.themoviedb.org/3/movie/${movieId}/credits?api_key=f3e796f7e1ac95620354ed26f063ba80`)
        .then(response =>{
            // console.log(response.data.cast);
            this.setState({
                actors : response.data.cast,
            });
        })
    }

    componentDidMount(){
        this.getMovieInfo();
        this.getActorInfo();
    }
    
    render(){
    return(

        <div className='rmdb-movie'>
            <MovieClickInfo info={this.state.movieInfo}/>
            {!this.state.actors?<Loader /> : <Actors actors = {this.state.actors}/>}
            
        </div>
        
    );
    }
}

export default MovieInfo