import React,{Component} from 'react';
import axios from 'axios';
import SingleActorInfo from './../SingleActorInfo/SingleActorInfo';

class ActorInfo extends Component{

    state={
        actorInfo:null,
    };
    
    getActorInfo = () => {
        const personId = this.props.match.params.personId;
        // console.log(personId);
        axios.get(` https://api.themoviedb.org/3/person/${personId}?api_key=f3e796f7e1ac95620354ed26f063ba80&language=en-US`)
        .then(response =>{
            // console.log(response);
              this.setState({
                actorInfo : response.data,
              });
        })
    };
    componentDidMount(){
        this.getActorInfo();
    }

    render(){
        // console.log(this.props.match.params.personId);
        return(
            <div>
               {this.state.actorInfo ? <SingleActorInfo info={this.state.actorInfo}/> : null}
            </div>
        );
    }
}

export default ActorInfo;