import React from 'react';
import { BrowserRouter, Route } from "react-router-dom";
import Home from './component/home/Home';
import MovieInfo from './component/MovieInfo/MovieInfo';
import SearchMovie from './component/SearchMovie/SearchMovie';
import ActorInfo from './component/ActorInfo/ActorInfo';
import Header from './component/header/Header';


function App() {
  return (
    <div className="App">
      <Header />
      {/* <Home /> */}
      {/* <MovieInfo /> */}

      <BrowserRouter>
        <Route path="/" exact component={Home} />
        <Route path="/movie/:movieId" component={MovieInfo} />
        <Route path="/search" component={SearchMovie} />
        <Route path="/person/:personId" component={ActorInfo} />
        
      </BrowserRouter>
    </div>
  );
}

export default App;
