import React from 'react';
import './SearchAllMovie.css';
import { Link } from "react-router-dom";


function SearchAllMovie(props){
    console.log(props);
    
    return(
        <div className="rmdb-grid-content rmdb-home-grid rmdb-grid-element">
            
            <div className="rmdb-moviethumb">
            <Link to={`/movie/${props.info.id}`}>
            <img src={`http://image.tmdb.org/t/p/w500/${props.info.poster_path}`}
                        alt="moviethumb"/>
            </Link>
            </div>
        

    </div>);
}

export default SearchAllMovie;