import React,{Component} from 'react';
import axios from 'axios';
import SearchAllMovie from '../SearchAllMovie/SearchAllMovie';

class SearchMovie extends Component{


    state={
        movies:null,
        query:''
    };

    searchHandler=event=>{
        let newValue = event.target.value;
        this.setState({query :newValue});
        // console.log(this.state.query);
    }

    clickHandler=event=>{
        event.preventDefault();
        axios.get(`https://api.themoviedb.org/3/search/movie?api_key=f3e796f7e1ac95620354ed26f063ba80&language=en-US&https://api.themoviedb.org/3/search/movie?api_key=f3e796f7e1ac95620354ed26f063ba80&language=en-US&query=${this.state.query}&page=1&include_adult=falsepage=1&include_adult=false`)
        .then(response => {
            // console.log(response);
            this.setState({
                movies:response.data.results
            });
            // console.log(this.state.movies);
            
        })
        
    }
    render(){
        let singleMovie = this.state.movies ? this.state.movies.map((movie, index) => {
            return <SearchAllMovie info={movie} key={index} />
        }) : null;
        
      return(
      <div>

      
        <div className="rmdb-searchbar">
            <div className="rmdb-searchbar-content">
                <span aria-hidden="true" className="fa fa-search fa-2x rmdb-fa-search"></span>
                
  
                <form onSubmit={this.clickHandler}>
                     <input type="text" className="rmdb-searchbar-input" placeholder="Search" onChange={event =>this.searchHandler(event)}/>
                    
                     
                     
                     
                </form>
                </div>

        </div>
        {/* {this.state.movies?<SearchAllMovie info={this.state.movies}/>:null} */}
        {singleMovie}
        
        </div>
    );
    }
    
}


export default SearchMovie;