import React,{Component} from 'react';
import axios from 'axios';
import Search from './../search/Search';
import Movies from './../Movies/Movies';
import HeroImage from './../heroImage/HeroImage';
import Loader from './../Loader/Loader';
import LoadMore from './../LoadMore/LoadMore';
import './home.css';

class Home extends Component{

    state = {
        movies : null,
        currentPage : 1,
        heroImage : null,
        newMovies :null
    };

    getMovies = () => {
        axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=f3e796f7e1ac95620354ed26f063ba80&language=en-US&page=${this.state.currentPage}`)
        .then(response => {
            this.setState({
                movies : response.data.results,
                currentPage : response.data.page,
                heroImage : response.data.results[0],
                // moreMovies : [...this.state.movies]
                
                
            });
            
        })

    }

    clickHandler=event=>{
        event.preventDefault();
        let moreMovies = [...this.state.movies];
        moreMovies.push(this.state.newMovies);
        this.setState({
            currentPage : this.state.currentPage+1,
            movies : this.state.moreMovies
        })        
        }

    
    
    

    componentDidMount(){
        this.getMovies();
    }

    render(){  
    return(
        <div>
             <HeroImage hero={this.state.heroImage}/>
             <Search />
             <div className='rmdb-home rmdb-home-grid'>
             { !this.state.movies ? <Loader /> : <Movies movies = {this.state.movies} currentPage={this.state.currentPage}/>}
             </div>
             <LoadMore currentPage={this.state.currentPage} onSubmit={this.clickHandler}/>
             <button>ok</button>
             
        </div>
        
    );
    }
}

export default Home;