import React from 'react';
import logo from './../../assets/img/reactMovie_logo.png';
import logo2 from './../../assets/img/tmdb_logo.png';
import './header.css';


function Header(){
    return(
        <div className="rmdb-header">
            <div className="rmdb-header-content">
                <a href="/">
                    <img className="rmdb-logo" src={logo} alt="rmdb-logo" />
                </a>
                <img className="rmdb-tmdb-logo" src={logo2} alt="tmdb-logo" />
            </div>
        </div>
    );
}

export default Header;